.moireWrapper {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: moireX 4s -4s ease-in-out infinite alternate;
          animation: moireX 4s -4s ease-in-out infinite alternate;
}

.moire {
  -webkit-animation: moireY 4s -2s ease-in-out infinite alternate;
          animation: moireY 4s -2s ease-in-out infinite alternate;
}

@-webkit-keyframes moireX {
    from {-webkit-transform: translate(4%, 0);transform: translate(4%, 0);}
    to {-webkit-transform: translate(-4%, 0);transform: translate(-4%, 0);}
}

@keyframes moireX {
    from {-webkit-transform: translate(4%, 0);transform: translate(4%, 0);}
    to {-webkit-transform: translate(-4%, 0);transform: translate(-4%, 0);}
}

@-webkit-keyframes moireY {
    from {-webkit-transform: translate(0, 4%);transform: translate(0, 4%);}
    to {-webkit-transform: translate(0, -4%);transform: translate(0, -4%);}
}

@keyframes moireY {
    from {-webkit-transform: translate(0, 4%);transform: translate(0, 4%);}
    to {-webkit-transform: translate(0, -4%);transform: translate(0, -4%);}
}



